import React from "react";
import { SERVICES } from "../text";
import styled from "styled-components";

const PriceBadgeStyle = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-wrap: wrap;

  height: 40px;
  width: ${(props) => (props.isFree ? "200%" : "100%")};
  margin-left: ${(props) => (props.isFree ? "-100%" : "0")};
  padding: ${(props) => props.theme.space.xs};
  border-radius: ${(props) => props.theme.radius.xs};
  background-color: ${(props) =>
    props.theme[props.color](props.primary ? 0.1 : 0.05)};
  color: ${(props) =>
    props.isIcon ? props.theme[props.color]() : "currentColor"};
`;

const angleTop = "60%";
const angleBottom = "40%";
const iconOffset = "-10px";

const SplitBadgeContainer = styled.div`
  position: relative;
  height: 40px;
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  ${PriceBadgeStyle} {
    position: absolute;

    &:first-child {
      clip-path: polygon(0 0, ${angleTop} 0, ${angleBottom} 100%, 0 100%);
      svg {
        margin-left: ${iconOffset};
      }
    }

    &:last-child {
      clip-path: polygon(${angleTop} 0, 100% 0, 100% 100%, ${angleBottom} 100%);
      svg {
        margin-right: ${iconOffset};
      }
    }
  }
`;

const PriceBadge = ({
  primary = false,
  isIcon = false,
  children,
  serviceIds,
}) => {
  const isFree = children === "";

  if (typeof serviceIds === "string") {
    return (
      <PriceBadgeStyle
        color={SERVICES[serviceIds].color}
        primary={primary}
        isIcon={isIcon}
        title={SERVICES[serviceIds].name}
        isFree={isFree}
      >
        {isIcon ? SERVICES[serviceIds].icon : children}
      </PriceBadgeStyle>
    );
  }

  return (
    <SplitBadgeContainer>
      <PriceBadge
        serviceIds={serviceIds[0]}
        isIcon={isIcon}
        primary={primary}
      />
      {!isIcon && <div>{children}</div>}
      <PriceBadge
        serviceIds={serviceIds[1]}
        isIcon={isIcon}
        primary={primary}
      />
    </SplitBadgeContainer>
  );
};

export default PriceBadge;
