import React from "react";
import styled from "styled-components";

import Section from "../components/Section";

import text, { SERVICE_IDS, sectionsList } from "../text";
import PriceTable from "../components/PriceTable";
import ServiceCard from "../components/ServiceCard";

const Container = styled.div`
  position: relative;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: ${(props) => props.theme.space.md};

  @media (max-width: 1024px) {
    display: flex;
    flex-direction: column-reverse;
  }
`;

const ServicesDescriptionContainer = styled.div`
  position: sticky;
  top: calc(60px + ${(props) => props.theme.space.im});
  height: max-content;

  > * + * {
    margin-top: ${(props) => props.theme.space.im};
  }

  @media (max-width: 1024px) {
    position: relative;
    top: 0;
  }
`;

const PricePage2 = () => {
  const displayedServiceIds = [
    SERVICE_IDS.complex,
    SERVICE_IDS.hygienic,
    SERVICE_IDS.trimming,
    SERVICE_IDS.expressShedding,
    SERVICE_IDS.cats,
    SERVICE_IDS.ultrasound,
  ];

  return (
    <Section heading={text.price.title} main>
      <Container>
        <div>
          {sectionsList.map((section, index) => (
            <PriceTable
              section={section}
              firstTable={index === 0}
              key={section.name}
            />
          ))}
        </div>
        <ServicesDescriptionContainer>
          {displayedServiceIds.length > 0 &&
            displayedServiceIds.map((serviceId) => (
              <ServiceCard serviceId={serviceId} key={serviceId} />
            ))}
          {displayedServiceIds.length === 0 && <div>Warning</div>}
        </ServicesDescriptionContainer>
      </Container>
    </Section>
  );
};

export default PricePage2;
