import React from "react";
import styled from "styled-components";

import Heading from "./Heading";
import PriceBadge from "./PriceBadge";

import text, { SERVICES } from "../text";

const Table = styled.table`
  table-layout: fixed;
  width: 100%;
  margin-bottom: ${(props) => props.theme.space.lg};
  border-spacing: 4px;

  td,
  th {
    border-radius: ${(props) => props.theme.radius.xs};
    padding: 0;
    font-weight: 500;

    &:first-child {
      padding-left: ${(props) => props.theme.space.im};
    }
  }

  th {
    vertical-align: baseline;
    h1 {
      font-weight: 300;
    }
  }

  caption {
    text-align: right;
    padding: 0 ${(props) => props.theme.space.md};
    ${(props) => props.theme.fontSmall};
    font-weight: 300;
  }

  tbody {
    tr {
      &:hover {
        background: linear-gradient(
          90deg,
          #e5edf5 0%,
          rgba(229, 237, 245, 0) 100%
        );
      }
    }
  }
`;

const DividerStyle = styled.div`
  height: 4px;
  border-radius: ${(props) => props.theme.radius.xs};
  background-color: ${(props) => props.theme.neutralTransparency};
  margin: ${(props) => props.theme.space.im} 0;
`;
const TableDivider = ({ colSpan }) => {
  return (
    <tr>
      <td colSpan={colSpan}>
        <DividerStyle />
      </td>
    </tr>
  );
};
const FixedTd = styled.td`
  width: 72px;
`;
const FixedTh = styled.th`
  width: 72px;
`;
const TableHelperText = styled.div`
  ${(props) => props.theme.fontSmall}
  font-weight: 300;
`;

const TableWarningText = styled.div`
  background-color: ${(props) => props.theme.red(1)};
  color: ${(props) => props.theme.textWhite};
  padding: ${(props) => props.theme.space.sm};
  border-radius: ${(props) => props.theme.radius.xs};
`;

const NonHoverableTRow = styled.tr`
  &:hover {
    background: none !important;
  }
`;

const List = styled.ul`
  margin: 0;
  padding-left: ${(props) => props.theme.space.md};
  padding-top: ${(props) => props.theme.space.md};
  padding-bottom: ${(props) => props.theme.space.md};
  font-weight: 300;

  li + li {
    margin-top: ${(props) => props.theme.space.sm};
  }
`;

const CardList = styled.ul`
  margin: 0;
  padding: 0;
  list-style: none;

  li {
    background-color: ${(props) => props.theme[props.color](0.1)};
    font-weight: 300;
    padding: ${(props) => props.theme.space.sm};
    border-radius: ${(props) => props.theme.radius.xs};
  }

  li + li {
    margin-top: ${(props) => props.theme.space.sm};
  }
`;

const PriceTable = ({ section, firstTable = false }) => {
  const firstServiceId =
    typeof section.services[0] === "string"
      ? section.services[0]
      : section.services[0][0];

  const mainColor = SERVICES[firstServiceId].color;

  let formatRangePrice = (price) => {
    if (typeof price === "string" && price.includes(" - ")) {
      let formatedPrice = price.split(" - ");
      return (
        <>
          <span style={{ fontSize: "0.9em", marginTop: "-2px"}}>{formatedPrice[0]}</span>
          <div style={{ width: "100%", lineHeight: 0.25, fontSize: "0.9em" }}>-</div>
          <span style={{ fontSize: "0.9em" }}>{formatedPrice[1]}</span>
        </>
      );
    }
    return price;
  };

  return (
    <Table key={section.name}>
      {firstTable && <caption>{text.price.serviceCaption}</caption>}
      <thead>
        <tr>
          <th>
            <Heading secondary>{section.name}</Heading>
          </th>
          {section.services.map((serviceId) => (
            <FixedTh key={serviceId}>
              <PriceBadge isIcon serviceIds={serviceId} />
            </FixedTh>
          ))}
        </tr>
        <TableDivider colSpan={section.services.length + 1} />
      </thead>
      <tbody>
        {section.helperText && (
          <NonHoverableTRow>
            <td colSpan={section.services.length + 1}>
              <TableHelperText>{section.helperText}</TableHelperText>
            </td>
          </NonHoverableTRow>
        )}
        {section.warning && (
          <NonHoverableTRow>
            <td colSpan={section.services.length + 1}>
              <TableWarningText>{section.warning}</TableWarningText>
            </td>
          </NonHoverableTRow>
        )}
        {section.helperList && (
          <NonHoverableTRow>
            <td colSpan={section.services.length + 1}>
              <List>
                {section.helperList.map((helperListText, i) => (
                  <li key={i}>{helperListText}</li>
                ))}
              </List>
            </td>
          </NonHoverableTRow>
        )}
        {section.prices &&
          section.prices.map((price, rowIndex) => (
            <tr key={price.name}>
              <td>
                <div>{price.name}</div>
                {price.helperText && (
                  <TableHelperText>{price.helperText}</TableHelperText>
                )}
              </td>
              {price.prices.map((priceAmount, index) => (
                <FixedTd key={priceAmount + index}>
                  <PriceBadge
                    primary={rowIndex % 2 === 0}
                    serviceIds={section.services[index]}
                  >
                    {formatRangePrice(priceAmount)}
                  </PriceBadge>
                </FixedTd>
              ))}
            </tr>
          ))}
        {section.descriptionList && (
          <NonHoverableTRow>
            <td colSpan={section.services.length + 1}>
              <CardList color={mainColor}>
                {section.descriptionList.map((descriptionListText, i) => (
                  <li key={`listitem${i}`}>{descriptionListText}</li>
                ))}
              </CardList>
            </td>
          </NonHoverableTRow>
        )}
      </tbody>
    </Table>
  );
};

export default PriceTable;
