import React from "react";
import Heading from "./Heading";
import { SERVICES } from "../text";
import styled from "styled-components";

const Card = styled.div`
  border-radius: ${(props) => props.theme.radius.sm};
  background-color: ${(props) => props.theme[props.color](0.1)};
  padding: ${(props) => props.theme.space.md};

  svg {
    color: ${(props) => props.theme[props.color]()};
  }

  h1 {
    text-align: left;
    display: flex;
    align-items: center;
    gap: ${(props) => props.theme.space.im};
    font-weight: 300;
  }

  p {
    margin: ${(props) => props.theme.space.sm} 0;
  }
`;

const ServiceCard = ({ serviceId }) => {
  return (
    <Card color={SERVICES[serviceId].color}>
      <Heading secondary>
        {SERVICES[serviceId].icon} {SERVICES[serviceId].name}
      </Heading>
      <p>{SERVICES[serviceId].description}</p>
    </Card>
  );
};

export default ServiceCard;
